import React, { useMemo, useState } from 'react';
import './AddActionForm.css'
import { addAction } from '../../db';
import { useSelector } from 'react-redux';

const AddActionForm = ({ habitId }) => {
    const [actionValue, setActionValue] = useState(null);
    const time = useSelector(state => state.date)
    const disabledActionBtn = useMemo(() => actionValue <= 0 || !actionValue || !Number(actionValue), [actionValue])

    const changeActionValue = e => setActionValue(e.target.value);

    const addNewAction = async (e) => {
        e.preventDefault()
        if (!habitId) return;

        await addAction(habitId, Number(actionValue), time)
        document.getElementById('addActionsForm').reset();
    }

    return (
        <form id='addActionsForm' onSubmit={addNewAction}>
            <input placeholder='Сколько прошли до цели' type="number" onChange={changeActionValue} />

            <button onClick={addNewAction} type='button' className='addActions-btn' disabled={disabledActionBtn}>
                <svg width="16" height="12" viewBox="0 0 16 12" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <path d="M1 7L5 11L15 1" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" />
                </svg>{/*check.svg */}
            </button>
        </form>
    );
}

export default AddActionForm;
