import React, { memo, useEffect, useState } from 'react';
import './HabitsList.css'
import { beInProgressStatus, complitedStatus, db, notComplitedStatus } from '../../db';
import LoaderHorizontally from '../../ui/LoaderHorizontally/LoaderHorizontally'
import { useLiveQuery } from 'dexie-react-hooks';
import SetFilterBtn from '../../ui/SetFilterBtn/SetFilterBtn';
import HabitElement from '../../components/HabitElement/HabitElement';

const HabitsList = memo(() => {
    const habitsFromDB = useLiveQuery(() => db.habits.reverse().toArray());//[{habit}]

    const [inProcess, setInProcess] = useState(null);
    const [complited, setComplited] = useState(null);
    const [notComplited, setNotComplited] = useState(null);

    const [visibleFilter, setVisibleFilter] = useState('all');

    useEffect(() => {
        const filterByStatus = () => {
            if (!habitsFromDB) return

            if (habitsFromDB.lenght === 0) {
                setInProcess([])
                setComplited([])
                setNotComplited([])
                return;
            }
            const inProcessArray = []
            const complitedArray = []
            const notComplited = []

            habitsFromDB.forEach(el => {
                switch (el.status) {
                    case beInProgressStatus:
                        inProcessArray.push(el)
                        break;
                    case complitedStatus:
                        complitedArray.push(el)
                        break;
                    case notComplitedStatus:
                        notComplited.push(el)
                        break;
                    default:
                        break;
                }
            });

            setInProcess(inProcessArray)
            setComplited(complitedArray)
            setNotComplited(notComplited)
        }
        filterByStatus()
    }, [habitsFromDB]);

    const renderHibits = (list) => {
        if (list.length === 0) return (<h5 className='noHaveHabits'>У вас нет привычек.</h5>)

        return list.map(i => (<HabitElement key={i.id} data={i} />))
    }

    return (
        <article className='HabitsList'>
            <div className='filterStatus-box'>
                <SetFilterBtn filter={visibleFilter} setFilter={setVisibleFilter}
                    filterKey={'all'}>Все</SetFilterBtn>

                <SetFilterBtn filter={visibleFilter} setFilter={setVisibleFilter}
                    filterKey={beInProgressStatus}>В процесе</SetFilterBtn>

                <SetFilterBtn filter={visibleFilter} setFilter={setVisibleFilter}
                    filterKey={complitedStatus}>Завершённые</SetFilterBtn>

                <SetFilterBtn filter={visibleFilter} setFilter={setVisibleFilter}
                    filterKey={notComplitedStatus}>Проваленые</SetFilterBtn>
            </div>

            <div className='list'>
                {habitsFromDB ?
                    (<>
                        {visibleFilter === 'all' ? renderHibits(habitsFromDB) : ''}
                        {visibleFilter === beInProgressStatus ? renderHibits(inProcess) : ''}
                        {visibleFilter === complitedStatus ? renderHibits(complited) : ''}
                        {visibleFilter === notComplitedStatus ? renderHibits(notComplited) : ''}
                    </>)
                    : (<LoaderHorizontally />)
                }
            </div>
        </article>
    );
})

export default HabitsList;
