import React from 'react';
import { useSelector } from 'react-redux';
import { Link } from 'react-router-dom';
import { NavigatePath, listPath, settingsPath, shopPath } from '../../routes';
import { icons } from './icons';
import CoinsView from '../../ui/CoinsView/CoinsView';
import HealthView from '../../ui/HealthView/HealthView';
import './UserMenu.css'

const UserMenu = () => {
    const user = useSelector(state => state.user)

    const stopPropagation = (e) => e.stopPropagation()
    return (
        <div className='UserMenu' onClick={stopPropagation}>
            <div className='UserMenu-userStates'>
                <HealthView health={user.health} />
                <CoinsView count={user.coins} />
            </div>
            <nav className='navigate'>
                <Link to={NavigatePath(listPath)} className='navigate-item'>
                    <div className='icon-box'>
                        {icons.home}
                    </div>
                    <h3 className='title'>Домашняя</h3>
                </Link>
                <Link to={NavigatePath(shopPath)} className='navigate-item'>
                    <div className='icon-box'>
                        {icons.shop}
                    </div>
                    <h3 className='title'>Магазин</h3>
                </Link>
                <Link to={NavigatePath(settingsPath)} className='navigate-item'>
                    <div className='icon-box'>
                        {icons.library}
                    </div>
                    <h3 className='title'>Библиотека</h3>
                </Link>
                <Link to={NavigatePath(settingsPath)} className='navigate-item'>
                    <div className='icon-box'>
                        {icons.settings}
                    </div>
                    <h3 className='title'>Настройки</h3>
                </Link>
            </nav>
        </div>
    );
}

export default UserMenu;
