import React, { memo, useMemo } from 'react';
import { beInProgressStatus, complitedStatus, notComplitedStatus } from '../../db';
import { HabitElementIcons } from '../../components/HabitElement/icons';

const TargetStatus = memo(({ targetValue, status }) => {

    const renderStatus = useMemo(() => {
        if (status === complitedStatus || (status === beInProgressStatus && targetValue === 0))
            return (<h4>Цель достигнута!</h4>)

        if (status === notComplitedStatus)
            return (<h4>Цель оказалась не посильной!</h4>)

        if (status === beInProgressStatus)
            return (<h4>До цели осталось: {targetValue}</h4>)

        return '';
    }, [targetValue, status])

    return (<>
        {targetValue !== null && status ?
            (<div className='habit-info targetStatus'>
                {HabitElementIcons.target}
                {renderStatus}
            </div>) : ''
        }
    </>);
})

export default TargetStatus;
