import React from 'react';
import ModalLayout from '../../pages/ModalLayout';
import './ActionModal.css'
import { beInProgressStatus, db } from '../../db';
import { useLiveQuery } from 'dexie-react-hooks';
import ActionHistory from '../../components/ActionHistory/ActionHistory';
import TargetStatus from '../../ui/TargetStatus/TargetStatus';
import AddActionForm from '../../components/AddActionForm/AddActionForm';

const ActionModal = ({ data }) => {
    const { habitId } = data;

    const habit = useLiveQuery(() => db.habits.where('id').equals(habitId).toArray())?.at(0);//[{user}]

    const status = habit?.status;
    const targetValue = habit?.targetValue;

    return (
        <ModalLayout modalTitle={
            status === beInProgressStatus && targetValue ?
                'Добавить действие' : 'Просмотр истории действий'}>

            <h3 className={`ActionModal-HabitTitle ${true ? '' : 'load'}`}>
                {habit?.title}
            </h3>

            <TargetStatus targetValue={targetValue} status={status} />

            {Boolean(targetValue) && <AddActionForm habitId={habit.id} />}

            <ActionHistory habitId={habitId} />
        </ModalLayout>
    );
}

export default ActionModal;
