export const levelCosts = [5, 10, 20, 35, 50, 70]
const levelsCount = levelCosts.length;
//сколько очков нужно набрать чтобы повысить уровень
//номер уровня == индекс в массиве + 1

export const getLevelCompleted = (exp, level) => {
    const levCost = level > levelsCount ? levelCosts[levelsCount - 1] : levelCosts[level - 1];
    const percent = (exp / levCost) * 100
    return percent >= 100 ? 100 : percent;
}

export const getProgressExperience = (exp, level) => {
    const levelCost = level > levelsCount ? levelCosts[levelsCount - 1] : levelCosts[level - 1];

    if (exp < levelCost) return [exp, level];

    const newLevel = level + 1;
    const newExpState = exp - levelCost;

    return getProgressExperience(newExpState, newLevel);
}

export const levelSettings = [{ maxHealth: 1 }, { maxHealth: 2 }, { maxHealth: 5 }, { maxHealth: 5 }]
const levelSettingsCount = levelSettings.length

export const getLevelSettings = (level) => {
    return level > levelSettingsCount ?
        levelSettings[levelSettingsCount - 1] :
        levelSettings[level - 1];
}