import React from 'react';
import UserEventsLayout from '../UserEventsLayout';
import { useDispatch, useSelector } from 'react-redux';
import CoinsView from '../../../ui/CoinsView/CoinsView';
import { setUser } from '../../../store';
import { db } from '../../../db';
import { getLevelSettings } from '../../../gamification';

const NullHealth = ({ setEvents }) => {
    const dispatch = useDispatch()
    const userState = useSelector(state => state.user)

    const restorationHealthCost = 1000;
    const { coins, name, level } = userState;

    const payEvent = async () => {
        if (coins < restorationHealthCost) return;
        const newCoinsState = coins - restorationHealthCost;
        const maxHealth = getLevelSettings(level).maxHealth;

        await db.user.update(name, { coins: newCoinsState, health: maxHealth })
        setEvents(null)
    }

    const closeMenu = async () => {
        await db.user.update(name, { daysActivity: 0, lastActivity: null })
        db.activities.clear()

        const maxHealth = getLevelSettings(level).maxHealth;
        dispatch(setUser({
            ...userState,
            daysActivity: 0,
            health: maxHealth
        }))
        setEvents(null)
    }
    return (
        <UserEventsLayout closeEvent={closeMenu}>
            <div className='UserEventsLayout-item NullHealth'>
                <div className='image-box'>
                    <svg width="75" height="60" viewBox="0 0 75 60" fill="none" xmlns="http://www.w3.org/2000/svg">
                        <path d="M9.75275 37.9772C4.48732 34.5732 0.92446 28.7818 0.691109 22.0995C0.309419 11.1693 8.98039 1.99511 20.0583 1.60827C25.7189 1.41059 30.9128 3.54565 34.6896 7.13943L32.3121 10.6861L36.4318 14.0444L33.6081 19.1461L38.21 21.9872L33.9222 28.1406L38.5067 30.482L34.1665 35.1363L38.8208 39.4765L34.498 44.6305L37.6183 48.0237L34.7772 52.6257L36.4676 58.0562C35.0092 58.0143 33.5571 57.5031 32.3492 56.5122L9.75275 37.9772Z" />
                        <path d="M65.8903 36.5692L43.1436 55.8284C41.6859 57.0624 39.8525 57.5859 38.0777 57.4105L36.7702 51.8752L39.9254 47.4827L37.0494 43.8801L41.7211 39.0402L37.3809 34.3859L42.0352 30.0457L37.6252 27.3902L42.3319 21.5509L37.9393 18.3956L41.112 13.5034L37.2366 9.86592L39.8557 6.49369C43.6328 2.89992 48.8266 0.764861 54.4873 0.962535C65.5652 1.34939 74.2361 10.5236 73.8545 21.4537C73.6377 27.6606 70.5483 33.0988 65.8903 36.5692Z" />
                    </svg>
                </div>
                <div className='content-box'>
                    <p className='event-title'>
                        Вы потеряли всё здровье, заплатите
                        <span> {restorationHealthCost} </span>
                        чокопаев или дневной прогресс будет сброшен.
                    </p>
                    <CoinsView count={coins} />
                    <div className='manage-btns-box'>
                        {userState.coins >= restorationHealthCost ?
                            <button onClick={payEvent} className='manage-btns_pay'>Заплатить</button> : ''}

                        <button onClick={closeMenu} className='manage-btns_close'>Отказаться</button>
                    </div>
                </div>
            </div>
        </UserEventsLayout>
    );
}

export default NullHealth;
