import React, { memo, useEffect, useMemo, useState } from 'react';
import { useSelector } from 'react-redux';
import './Header.css'
import { getLevelCompleted } from '../../gamification';
import avatar from '../../images/avatar.jpg'
import HealthView from '../../ui/HealthView/HealthView';
import UserMenu from '../../components/UserMenu/UserMenu';
import CoinsView from '../../ui/CoinsView/CoinsView';

const Header = memo(() => {
    const activeMenuClass = 'activeUserMenu';
    const user = useSelector(state => state.user)
    const time = useSelector(state => state.date)
    const { daysActivity, experience, health, lastActivity, level, coins } = user;

    const [isUserMenuActive, setIsUserMenuActive] = useState(false);

    const progres = useMemo(() => getLevelCompleted(experience, level), [user])

    function isToday(date) {
        if (!date) return false;

        const today = time ? new Date(time) : new Date();
        const inputDate = new Date(date);

        // Устанавливаем время в полночь для обеих дат, чтобы сравнивать только даты
        today.setHours(0, 0, 0, 0);
        inputDate.setHours(0, 0, 0, 0);

        return today.getTime() === inputDate.getTime();
    }

    const todayActivityStatus = useMemo(() => isToday(lastActivity), [time, lastActivity])

    const openMenu = (e) => {
        e.stopPropagation();
        e.preventDefault();
        setIsUserMenuActive(isUserMenuActive ? false : true)

        const classList = document.querySelector('body').classList;
        classList.toggle(activeMenuClass)
    }

    useEffect(() => {
        if (isUserMenuActive) {
            const closeMenu = (e) => {
                if (isUserMenuActive) {
                    e.preventDefault();
                    setIsUserMenuActive(false)
                    const classList = document.querySelector('body').classList;
                    classList.remove(activeMenuClass)
                }
            }
            document.addEventListener('click', closeMenu)
            return () => {
                document.removeEventListener('click', closeMenu)
            }
        }
    }, [isUserMenuActive]);

    return (
        <header className='header'>
            <div className='container'>
                <div className='header-content'>
                    <div className='today-activity'>
                        {todayActivityStatus ? (
                            <svg width="25" height="30" viewBox="0 0 25 30" fill="none" xmlns="http://www.w3.org/2000/svg">
                                <g clipPath="url(#clip0_42_23560)">
                                    <g clipPath="url(#clip1_42_23560)">
                                        <path d="M1.068 16.6744L1.044 8.21544C1.039 6.33344 2.25 4.94144 4.056 5.24544C4.413 5.30544 4.998 5.49044 5.306 5.65544L6.997 6.56044L10.247 2.46344C10.7926 1.77583 11.6223 1.375 12.5 1.375C13.3777 1.375 14.2074 1.77583 14.753 2.46344L21.523 10.9994C23.1231 12.9387 23.9989 15.3742 24 17.8884C24 24.0334 18.843 28.9994 12.5 28.9994C6.157 28.9994 1 24.0334 1 17.8884C1 17.4804 1.023 17.0754 1.068 16.6744Z" fill="#FF9600" stroke="white" strokeWidth="2" />
                                        <path d="M9.012 17.0748C9.02645 17.0378 9.04561 17.0029 9.069 16.9708L11.719 13.3618C11.8931 13.1245 12.1697 12.9844 12.464 12.9844C12.7583 12.9844 13.0349 13.1245 13.209 13.3618L15.732 16.7978C16.5412 17.5926 16.9979 18.6786 17 19.8128C17 22.2058 14.985 24.1458 12.5 24.1458C10.015 24.1458 8 22.2058 8 19.8128C8 18.7738 8.38 17.8208 9.012 17.0748Z" fill="#FFC800" />
                                    </g>
                                </g>
                                <defs>
                                    <clipPath id="clip0_42_23560">
                                        <rect width="25" height="30" fill="white" />
                                    </clipPath>
                                    <clipPath id="clip1_42_23560">
                                        <rect width="25" height="30" fill="white" />
                                    </clipPath>
                                </defs>
                            </svg>) : (
                            <svg width="26" height="30" viewBox="0 0 26 30" fill="none" xmlns="http://www.w3.org/2000/svg">
                                <g clipPath="url(#clip0_41_22093)">
                                    <g clipPath="url(#clip1_41_22093)">
                                        <path id='mainInGreyIcon' d="M1.16175 16.6744L1.13775 8.21544C1.13275 6.33344 2.34375 4.94144 4.14975 5.24544C4.50675 5.30544 5.09175 5.49044 5.39975 5.65544L7.09075 6.56044L10.3407 2.46344C10.8863 1.77583 11.716 1.375 12.5938 1.375C13.4715 1.375 14.3012 1.77583 14.8468 2.46344L21.6168 10.9994C23.2169 12.9387 24.0926 15.3742 24.0938 17.8884C24.0938 24.0334 18.9368 28.9994 12.5938 28.9994C6.25075 28.9994 1.09375 24.0334 1.09375 17.8884C1.09375 17.4804 1.11675 17.0754 1.16175 16.6744Z" fill="#E5E5E5" strokeWidth="2" />
                                        <path id='holeInGreyIcon' d="M9.10575 17.0748C9.1202 17.0378 9.13936 17.0029 9.16275 16.9708L11.8128 13.3618C11.9868 13.1245 12.2635 12.9844 12.5578 12.9844C12.852 12.9844 13.1287 13.1245 13.3028 13.3618L15.8257 16.7978C16.6349 17.5926 17.0917 18.6786 17.0938 19.8128C17.0938 22.2058 15.0787 24.1458 12.5938 24.1458C10.1088 24.1458 8.09375 22.2058 8.09375 19.8128C8.09375 18.7738 8.47375 17.8208 9.10575 17.0748Z" />
                                    </g>
                                </g>
                            </svg>)}
                        <h4 className={`todayActivity ${todayActivityStatus ? 'active' : ''}`}>
                            {daysActivity || 0}
                        </h4>
                    </div>

                    <div className='level-bar'>
                        <div className='level'>{level || 0}L.</div>
                        <div className='line-experience'><span className='progres' style={{ 'width': `${progres}%` }}></span></div>
                    </div>

                    <div className='computerUserStates'>
                        <CoinsView count={coins} />
                        <HealthView health={health} />
                    </div>

                    <button id='openUserMenu' onClick={openMenu} className='userMenu-openBtn'>
                        <img src={avatar} />
                    </button>
                </div>
                {isUserMenuActive ? <UserMenu /> : ''}
            </div>
        </header >
    );
})

export default Header;
