export const daysDifference = (newDate, oldDate) => {
    const millisecondInHour = 3600000;

    const oldDateObj = new Date(oldDate);
    const newDateObj = new Date(newDate ?? new Date().toISOString());

    oldDateObj.setHours(0, 0, 0, 0);
    newDateObj.setHours(0, 0, 0, 0);

    const millisecInDay = millisecondInHour * 24;
    const daysDif = Math.floor((newDateObj.getTime() - oldDateObj.getTime()) / millisecInDay)
    return daysDif;
}