import { createStore } from 'redux';
import { composeWithDevTools } from 'redux-devtools-extension';
import { SET_DATE, SET_ISAUTH, SET_MODAL_ACTION, SET_USER, } from './constants';


const dateDefault = () => {
    const date = localStorage.getItem('appDate')
    return date || new Date().toISOString();
}
const defaultState = {
    user: {},
    isAuth: null,
    openModule: { type: null, data: {} },
    date: dateDefault(),
    theme: localStorage.getItem('appTheme') || 'light',
}

const reducer = (state = defaultState, action) => {
    switch (action.type) {
        case SET_USER:
            return { ...state, user: action.payload }
        case SET_ISAUTH:
            return { ...state, isAuth: action.payload }
        case SET_MODAL_ACTION:
            return { ...state, openModule: action.payload }
        case SET_DATE:
            return { ...state, date: action.payload }
        default:
            return state;
    }
}

export const setUser = (payload) => ({ type: SET_USER, payload });
export const setIsAuth = (payload) => ({ type: SET_ISAUTH, payload });
export const setModalAction = (payload) => ({ type: SET_MODAL_ACTION, payload });
export const setDateAction = (payload) => ({ type: SET_DATE, payload });

const store = createStore(reducer, composeWithDevTools())

export default store;