import React, { useEffect, useState } from 'react';
import './Shop.css'
import { ShopIcons } from './icons';
import { useSelector } from 'react-redux';
import { db } from '../../db';
import { daysDifference } from '../../Moment';
import OpenChest from '../../components/UserEvents/OpenChest/OpenChest';

const Shop = () => {
    const user = useSelector(state => state.user)
    const time = useSelector(state => state.date)
    const [isChestMenuOpen, setIsChestMenuOpen] = useState(false);
    const [userCanOpenChest, setUserCanOpenChest] = useState(null);

    const productsCost = { potion: 200 }
    const { coins, name, health, openChestDate } = user

    const buyPotion = async () => {
        const coinsDef = coins - productsCost.potion
        if (!coins || coinsDef < 0) return;

        await db.user.update(name, {
            coins: coinsDef,
            health: health + 1
        })
    }

    const getUerCanOpenChest = async () => {
        if (!openChestDate) {
            return setUserCanOpenChest(true)
        }
        if (daysDifference(time, openChestDate) > 0) {
            return setUserCanOpenChest(true)
        }
    }

    useEffect(() => {
        getUerCanOpenChest()
    }, [time, user.openChestDate])

    const openChest = async () => {
        if (!userCanOpenChest) return;
        const randomCountCoins = Math.floor(Math.random() * 21) + 10;
        //рандомное число умножается на 21, чтобы получить случайное число от 0 до 20.
        //затем прибавляем 10, чтобы получить случайное число от 10 до 30.
        setIsChestMenuOpen({
            plusCoins: randomCountCoins,
            coins
        })
        setUserCanOpenChest(false)
        await db.user.update(name, {
            coins: coins + randomCountCoins,
            openChestDate: time
        })
    }

    return (
        <>
            {isChestMenuOpen ? <OpenChest setEvent={setIsChestMenuOpen} data={isChestMenuOpen} /> : ''}
            <div className='Shop'>
                <div className='Shop-title'>
                    <h1>Магазин</h1>
                </div>
                <div className='Shop-content'>
                    <div className='Shop-products'>
                        <div className='products-item'>
                            <div className='product-image'>
                                {ShopIcons.potion}
                            </div>
                            <div className='product-content'>
                                <div className='product-description'>
                                    <h2 className='description-title'>
                                        Зелье здоровья
                                    </h2>
                                    <p>
                                        Прибавляет +1 к здоровью, позволяет вашему дневному режиму
                                        оставаться на месте в течение одного полного дня бездействия.
                                    </p>
                                </div>
                                <div className='product-buyBtn'>
                                    <button onClick={buyPotion} disabled={coins < productsCost.potion}>
                                        Купить один за {productsCost.potion}
                                    </button>
                                </div>
                            </div>
                        </div>
                        <div className='products-item'>
                            <div className='product-image'>
                                {ShopIcons.chest}
                            </div>
                            <div className='product-content'>
                                <div className='product-description'>
                                    <h2 className='description-title'>
                                        Дневной сундук
                                    </h2>
                                    <p>Заходите каждый день и забирайте награду из дневного сундука.</p>
                                </div>
                                <div className='product-buyBtn'>
                                    <button disabled={!userCanOpenChest} onClick={openChest}>
                                        Забрать
                                    </button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    );
}

export default Shop;
