import React from "react";
import { Routes, Route } from 'react-router-dom';
import HabitsListPage from "./pages/HabitsListPage";
import RegistrationPage from "./pages/RegistrationPage";
import { libraryPath, listPath, registrationPath, settingsPath, shopPath } from "./routes";
import SettingsPage from "./pages/SettingsPage";
import ShopPage from "./pages/ShopPage";
import LibraryPage from "./pages/LibraryPage";


const AppRouter = () => {
    return (
        <Routes >
            <Route path={registrationPath} element={<RegistrationPage />} />
            <Route path={listPath} element={<HabitsListPage />} />
            <Route path={settingsPath} element={<SettingsPage />} />
            <Route path={shopPath} element={<ShopPage />} />
            <Route path={libraryPath} element={<LibraryPage />} />
            <Route path={'*'} element={<HabitsListPage />} />
        </Routes>
    )
}
export default AppRouter;