import React, { useState } from 'react';
import { useDispatch } from 'react-redux';
import { setDateAction } from '../../store';
import './Settings.css'

const Settings = () => {
    const dispatch = useDispatch()
    const [Date, setDate] = useState('');
    const [Level, setLevel] = useState(0);


    const onSubmit = (e) => {
        e.preventDefault()

        localStorage.setItem('appDate', Date)
        dispatch(setDateAction(Date))

        setDate('')
    }

    const dateChange = (e) => setDate(e.target.value)
    const levelChange = (e) => setLevel(e.target.value)
    return (
        <div className='Settings'>
            <h2>Настроки для тестирования</h2>
            <form className='Settings-setStatesForm' onSubmit={onSubmit}>
                <input placeholder='yyyy-mm-ddThh:mm:ss.sssZ' onChange={dateChange} />
                <input placeholder='Какой уровень вам нужен' type='number' onChange={levelChange} />

                <button type='submit'>Сохранить</button>
            </form>
        </div>
    );
}

export default Settings;
