import Dexie from 'dexie';
import { getProgressExperience } from './gamification';

export const beInProgressStatus = 'beInProgress';
export const complitedStatus = 'complited';
export const notComplitedStatus = 'notComplited';

export const usersHabitsExpCost = 1;
export const usersHabitsCoinCost = 5;

export const db = new Dexie('myDatabase');
db.version(1).stores({
    user: 'name, level, coins, experience, health, daysActivity, lastActivity, openChestDate',
    actions: '++actionId, id, value, date',
    habits: '++id, title, category, addDate, period, targetValue, status, priceCoin, priceExp, startTarget',
    activities: 'date, count, checked'
});

const addActivity = async (date) => {
    try {
        const lastActivity = await db.activities.toCollection().last();
        const lastActivityDate = lastActivity?.date;

        let isDateEqual;

        if (!lastActivityDate) isDateEqual = false
        else {
            const lastDate = new Date(lastActivityDate);

            lastDate.setHours(0, 0, 0, 0);
            date.setHours(0, 0, 0, 0);

            isDateEqual = date.getTime() === lastDate.getTime()
        }
        const dateStr = date.toISOString()

        if (isDateEqual) {
            await db.activities.update(
                dateStr,
                { count: lastActivity?.count + 1 }
            )
        } else {
            await db.activities.add({
                date: dateStr,
                count: 1,
                checked: false
            })
        }

        const userKey = (await db.user.toCollection().first()).name;

        await db.user.update(userKey, { lastActivity: dateStr })
    } catch (error) {
        console.error(error)
    }
}

export const addAction = async (id, value, addDate = null) => {
    try {
        value = Number(value);
        if (!value) return;

        const habit = await db.habits.where('id').equals(id).first()

        if (!habit || !habit.targetValue) return;

        const date = addDate ? new Date(addDate) : new Date();
        const formattedDate = date.toISOString();

        await db.actions.add({
            id,
            value,
            date: formattedDate
        })

        const newTargetValue = habit.targetValue - value;
        const updateData = newTargetValue > 0 ?
            { targetValue: newTargetValue } : { targetValue: 0 }

        await db.habits.update(id, updateData)

        addActivity(date)
    } catch (error) {
        console.error(error)
    }
}

export const addHabit = async (data, addDate = null) => {
    const {
        title,
        category,
        period,
        targetValue,
        priceCoin,
        priceExp
    } = data;

    if (!title) return;
    const date = addDate ? new Date(addDate) : new Date();
    const formattedDate = date.toISOString();

    const value = targetValue ? targetValue : null;

    await db.habits.add({
        title,
        category: category || 'общие',
        addDate: addDate || formattedDate,
        period,
        targetValue: value,
        startTarget: value,
        status: beInProgressStatus,
        priceCoin: priceCoin || usersHabitsCoinCost,
        priceExp: priceExp || usersHabitsExpCost,
    })
}

export const setHabitSatusToComplete = async (id, date = null) => {
    try {
        if (!id && id !== 0) return;

        const habit = await db.habits.where('id').equals(id).first()
        if (!habit) return;

        await db.habits.update(id, { status: complitedStatus })

        const user = await db.user.toCollection().first()

        if (!user) return;

        const { level, experience, name, coins } = user;

        const lastActivity = (date ? new Date(date) : new Date())

        const [neWExperience, newLevel] = getProgressExperience(experience + habit.priceExp, level)

        const updateObject = {
            coins: coins + habit.priceCoin,
            level: newLevel,
            experience: neWExperience
        }

        await db.user.update(name, updateObject)

        addActivity(lastActivity)
    } catch (error) {
        console.error(error)
    }
}

export const refuseHabitsetStatus = async (id) => {
    try {
        if (!id && id !== 0) return;

        const habit = await db.habits.where('id').equals(id).first()
        if (!habit) return;

        await db.habits.update(id, { status: notComplitedStatus })
    } catch (error) {
        console.error(error)
    }
}

export const deleteHabit = async (id) => {
    try {
        if (!id && id !== 0) return;

        await db.habits.delete(id)

        await db.actions.filter(function (action) {
            return action.id === id;
        }).delete()
    } catch (error) {
        console.error(error)
    }
}