import React, { useMemo } from 'react';
import { db } from '../../db';
import { useLiveQuery } from 'dexie-react-hooks';
import './ActionsList.css'
import LoaderHorizontally from '../LoaderHorizontally/LoaderHorizontally';

const ActionsList = ({ habitId }) => {
    const actionFromDB = useLiveQuery(async () => {
        return db.actions
            .where('id')
            .equals(habitId)
            .reverse()
            .toArray()
    });//[{action}]

    const renderList = useMemo(() => {
        if (!actionFromDB) return <LoaderHorizontally />;

        if (actionFromDB.length === 0) return (<p>У вас нету действий</p>)

        return actionFromDB.reverse().map(({ actionId, date, value }) => (
            <div className='actionElement' key={actionId}>
                <h5>Количество: {value}</h5>
                <h6>{date.split('T')[0]}</h6>
            </div>
        ))
    }, [actionFromDB])

    return (
        <div className='ActionsList'>
            {renderList}
        </div>
    );
}

export default ActionsList;
