import React, { memo } from 'react';
import chocoPie from '../../images/chocoPie.png';
import './CoinsView.css'

const CoinsView = memo(({ count }) => {
    return (
        <div className='CoinsView'>
            <div className='CoinsView-box'>
                <span>{count}</span>
                <img src={chocoPie} />
            </div>
        </div>
    );
})

export default CoinsView;
