import React from 'react';
import './SetFilterBtn.css'

const SetFilterBtn = ({ filter, setFilter, filterKey, children }) => {
    const setFilterGenerate = () => setFilter(filterKey);
    const isActive = filter === filterKey
    return (
        <button onClick={setFilterGenerate} disabled={isActive}
            className={`filterStatus-item ${isActive ? 'active' : ''}`} >{children}</button>
    );
}

export default SetFilterBtn;
