import React, { memo, useMemo } from 'react';
import { useSelector } from 'react-redux';
import { daysDifference } from '../../Moment';
import { HabitElementIcons } from '../../components/HabitElement/icons';

const PeriodInfo = memo(({ addDate, period }) => {
    const time = useSelector(state => state.date)

    const daysLeft = useMemo(() => {
        let daysInPeriod = {
            daily: 1,
            weekly: 7,
            monthly: 30
        }[period];

        const daysPassed = daysDifference(time, addDate)
        return daysInPeriod - daysPassed;
    }, [period, time])


    return (
        <div className='habit-info'>
            {HabitElementIcons.period}
            <h4>Дней осталось:{daysLeft}</h4>
        </div>
    );
})

export default PeriodInfo;
