import React, { useEffect, useMemo, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { db } from '../../db';
import { setUser } from '../../store';
import BuyHealingPotion from '../../components/UserEvents/BuyHealingPotion/BuyHealingPotion';
import { useLiveQuery } from 'dexie-react-hooks';
import { BUY_HEALING_POTION_EVENT, LEVEL_UP, NULL_HEALTH_EVENT } from './EventConstants';
import NullHealth from '../../components/UserEvents/NullHealth/NullHealth';
import LevelUp from '../../components/UserEvents/LevelUp/LevelUp';
import { daysDifference } from '../../Moment';

const UserActivity = ({ user }) => {
    const time = useSelector(state => state.date)
    const userState = useSelector(state => state.user)
    const activitiesFromDB = useLiveQuery(() => db.activities.toArray());
    const dispatch = useDispatch()

    const [userEventType, setUserEventType] = useState(null);

    const daysPassedSinceLastActivity = useMemo(() => {
        if (!user || !user?.lastActivity) return null;
        const lastActivity = activitiesFromDB?.at(-1)

        return daysDifference(time, lastActivity?.date);
    }, [time, user?.lastActivity])

    const decreaseHealth = async () => {
        const daysDif = daysDifference(time, activitiesFromDB?.at(-1)?.date)

        const minusHealth = user.health - daysDif;
        const newHealth = minusHealth > 0 ? minusHealth : 0;
        //один день пропуска = минус одно здоровье
        await db.user.update(user.name, { health: newHealth })

        if (newHealth === 0) {
            setUserEventType(NULL_HEALTH_EVENT)
            //кинуть меню смерти :)
        } else {
            setUserEventType(BUY_HEALING_POTION_EVENT)
        }
        await db.activities.add({
            date: time,
            count: 0,
            checked: false,
        })
    }

    const increasingDayActivity = async () => {
        const lastActivity = await db.activities.toCollection()?.last()

        if (
            !lastActivity ||
            lastActivity?.checked ||
            lastActivity.count === 0
        ) return;

        const updateState = { daysActivity: user.daysActivity + 1 }

        await db.user.update(user.name, updateState)

        dispatch(setUser({
            ...userState,
            ...updateState,
            lastActivity: user.lastActivity
        }))

        await db.activities.update(lastActivity.date, { checked: true })
    }

    useEffect(() => {
        try {
            const daysDif = daysPassedSinceLastActivity;
            if (daysDif === null) return;

            if (daysDif > 1) {
                decreaseHealth()
            } else if (daysDif === 1 || daysDif === 0) {
                increasingDayActivity()
            }
        } catch (error) {
            console.log(error)
        }
    }, [daysPassedSinceLastActivity]);

    useEffect(() => {
        if (!user || !userState?.name) return;

        const { experience, coins, health, openChestDate } = user;
        const newState = {
            ...userState,
            experience,
            coins,
            health,
            openChestDate
        }
        dispatch(setUser(newState))
    }, [user?.experience, user?.coins, user?.health, user?.openChestDate])

    useEffect(() => {
        if (
            !user?.level ||
            !userState?.level ||
            user?.level === userState?.level
        ) return;

        const { level } = user;
        const newState = { ...userState, level }
        dispatch(setUser(newState))

        setUserEventType(LEVEL_UP)
    }, [user?.level]);

    return (
        <>
            {userEventType === BUY_HEALING_POTION_EVENT ?
                <BuyHealingPotion setEvents={setUserEventType} /> : ''}
            {userEventType === NULL_HEALTH_EVENT ?
                <NullHealth setEvents={setUserEventType} /> : ''}
            {userEventType === LEVEL_UP ?
                <LevelUp setEvents={setUserEventType} /> : ''}
        </>
    );
}

export default UserActivity;
