import React, { useEffect, useMemo } from 'react';
import './HabitElement.css'
import { addHabit, beInProgressStatus, db, deleteHabit, notComplitedStatus, refuseHabitsetStatus, setHabitSatusToComplete } from '../../db';
import { ADD_ACTION_MODAL } from '../../modalTypes';
import useModal from '../../hooks/useModal';
import TargetStatus from '../../ui/TargetStatus/TargetStatus';
import PeriodInfo from '../../ui/PeriodInfo/PeriodInfo';
import { useSelector } from 'react-redux';
import { daysDifference } from '../../Moment';

const HabitElement = ({ data }) => {
    const time = useSelector(state => state.date)

    const { id, title, category, addDate, period, targetValue, status } = data;
    const compliteBtn = useMemo(() => (!targetValue || targetValue === 0 ? true : false), [targetValue]);
    //если цели нету или она выполнена то мы видим кнопку

    const [setModal] = useModal(ADD_ACTION_MODAL)
    const openActionModal = () => setModal({ habitId: id })

    //функции действий с привычкой 
    const complitHabitEvent = () => {
        //если цели нет или она завершина
        if (!(!targetValue || targetValue === 0)) return;
        setHabitSatusToComplete(id, time)
    }
    const refuseHabitEvent = () => {
        if (targetValue === 0) return;
        refuseHabitsetStatus(id)
    }
    const copyHabitEvent = () => {
        if (status === beInProgressStatus) return;

        const copyData = {
            title, category, period, targetValue: data.startTarget
        }
        addHabit(copyData, time)
    }
    const deleteHabitEvent = () => {
        if (status === beInProgressStatus) return;
        deleteHabit(id)
    }


    useEffect(() => {
        let daysInPeriod = {
            daily: 1,
            weekly: 7,
            monthly: 30
        }[period];

        const daysPassed = daysDifference(time, addDate)
        const daysLeft = daysInPeriod - daysPassed;

        //при достижении периуда привычка считается не выполненой
        if (daysLeft <= 0 && status === beInProgressStatus) {
            db.habits.update(id, { status: notComplitedStatus })
        }
    }, [time]);

    return (
        <div className={`HabitElement ${status}`}>
            <div className='HabitElement-content'>
                <div className='HabitElement-header'>
                    <h3 className='habit-title'>{title}</h3>
                </div>
                <div className='HabitElement-info-box'>

                    <TargetStatus status={status} targetValue={targetValue} />
                    <PeriodInfo addDate={addDate} period={period} />

                    <div className='habit-info'>
                        <svg width="19" height="19" viewBox="0 0 19 19" fill="none" xmlns="http://www.w3.org/2000/svg">
                            <path d="M4.90704 17.6623L8.90704 1.66226M10.907 17.6623L14.907 1.66226M3.90704 6.66226H17.907M1.90704 12.6623H15.907" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" />
                        </svg>
                        <h4>{category}</h4>
                    </div>
                </div>
            </div>

            <div className='HabitElement-manageMenu'>
                {status === beInProgressStatus ?
                    (<>
                        {compliteBtn &&
                            (<button onClick={complitHabitEvent}
                                className='HabitElement-manageMenu-item complite-btn'>

                                <svg width="16" height="12" viewBox="0 0 16 12" fill="none" xmlns="http://www.w3.org/2000/svg">
                                    <path d="M1 7L5 11L15 1" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" />
                                </svg>
                            </button>)}

                        {targetValue !== null &&
                            (<button onClick={openActionModal}
                                className='HabitElement-manageMenu-item addAction-btn'>

                                <svg width="16" height="20" viewBox="0 0 16 20" fill="none" xmlns="http://www.w3.org/2000/svg">
                                    <path d="M5 3H3C2.46957 3 1.96086 3.21071 1.58579 3.58579C1.21071 3.96086 1 4.46957 1 5V17C1 17.5304 1.21071 18.0391 1.58579 18.4142C1.96086 18.7893 2.46957 19 3 19H13C13.5304 19 14.0391 18.7893 14.4142 18.4142C14.7893 18.0391 15 17.5304 15 17V5C15 4.46957 14.7893 3.96086 14.4142 3.58579C14.0391 3.21071 13.5304 3 13 3H11M5 3C5 3.53043 5.21071 4.03914 5.58579 4.41421C5.96086 4.78929 6.46957 5 7 5H9C9.53043 5 10.0391 4.78929 10.4142 4.41421C10.7893 4.03914 11 3.53043 11 3M5 3C5 2.46957 5.21071 1.96086 5.58579 1.58579C5.96086 1.21071 6.46957 1 7 1H9C9.53043 1 10.0391 1.21071 10.4142 1.58579C10.7893 1.96086 11 2.46957 11 3M8 10H11M8 14H11M5 10H5.01M5 14H5.01" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" />
                                </svg>
                            </button>)}

                        {targetValue !== 0 &&
                            (<button onClick={refuseHabitEvent}
                                className='HabitElement-manageMenu-item refuse-btn'>

                                <svg width="14" height="14" viewBox="0 0 14 14" fill="none" xmlns="http://www.w3.org/2000/svg">
                                    <path d="M1 13L13 1M1 1L13 13" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" />
                                </svg>
                            </button>)}
                    </>) :
                    (<>
                        <button onClick={copyHabitEvent}
                            className='HabitElement-manageMenu-item tryAgain-btn'>

                            <svg width="18" height="18" viewBox="0 0 18 18" fill="none" xmlns="http://www.w3.org/2000/svg">
                                <path d="M5 13H3C2.46957 13 1.96086 12.7893 1.58579 12.4142C1.21071 12.0391 1 11.5304 1 11V3C1 2.46957 1.21071 1.96086 1.58579 1.58579C1.96086 1.21071 2.46957 1 3 1H11C11.5304 1 12.0391 1.21071 12.4142 1.58579C12.7893 1.96086 13 2.46957 13 3V5M7 17H15C15.5304 17 16.0391 16.7893 16.4142 16.4142C16.7893 16.0391 17 15.5304 17 15V7C17 6.46957 16.7893 5.96086 16.4142 5.58579C16.0391 5.21071 15.5304 5 15 5H7C6.46957 5 5.96086 5.21071 5.58579 5.58579C5.21071 5.96086 5 6.46957 5 7V15C5 15.5304 5.21071 16.0391 5.58579 16.4142C5.96086 16.7893 6.46957 17 7 17Z" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" />
                            </svg>
                        </button>
                        <button onClick={deleteHabitEvent}
                            className='HabitElement-manageMenu-item remove-btn'>
                            <svg width="18" height="20" viewBox="0 0 18 20" fill="none" xmlns="http://www.w3.org/2000/svg">
                                <path d="M16 5L15.133 17.142C15.0971 17.6466 14.8713 18.1188 14.5011 18.4636C14.1309 18.8083 13.6439 19 13.138 19H4.862C4.35614 19 3.86907 18.8083 3.49889 18.4636C3.1287 18.1188 2.90292 17.6466 2.867 17.142L2 5M7 9V15M11 9V15M12 5V2C12 1.73478 11.8946 1.48043 11.7071 1.29289C11.5196 1.10536 11.2652 1 11 1H7C6.73478 1 6.48043 1.10536 6.29289 1.29289C6.10536 1.48043 6 1.73478 6 2V5M1 5H17" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" />
                            </svg>
                        </button>
                    </>)
                }
            </div>
        </div>
    );
}

export default HabitElement;
