import { useEffect, useState } from 'react';
import AppRouter from './appRouter';
import './App.css';
import { useDispatch, useSelector } from 'react-redux';
import { useLiveQuery } from "dexie-react-hooks";
import { db } from './db';
import { useLocation, useNavigate } from 'react-router-dom';
import { NavigatePath, registrationPath } from './routes';
import { setDateAction, setIsAuth, setUser } from './store';
import AddActionModal from './modules/ActionModal/ActionModal';
import { ADD_ACTION_MODAL } from './modalTypes';
import UserActivity from './modules/UserActivity/UserActivity';
import LoaderWindow from './components/LoaderWindow/LoaderWindow';
import { daysDifference } from './Moment';
import { createNotification } from './notifications';
import logo from './images/logo.svg'

function App() {
  const location = useLocation();
  const time = useSelector(state => state.date)
  const user = useSelector(state => state.user)
  const isAuth = useSelector(state => state.isAuth)
  const theme = useSelector(state => state.theme)
  const modal = useSelector(state => state.openModule);//{type: str, data: {}}

  const [isLoaderActive, setIsLoaderActive] = useState(true);

  const navigate = useNavigate()
  const dispatch = useDispatch()

  const usersFromDB = useLiveQuery(() => db.user.toArray());//[{user}]

  useEffect(() => {
    const appTheme = localStorage.getItem('appTheme');
    if (!appTheme || !['light', 'dark'].includes(appTheme)) {
      localStorage.setItem('appTheme', 'light');
    }
  }, []);

  useEffect(() => {
    const body = document.querySelector('body');
    body.setAttribute('theme', `${theme}-theme`)
  }, [theme]);

  useEffect(() => {
    const goToRegistration = () => {
      dispatch(setIsAuth(false))
      navigate(NavigatePath(registrationPath))
    }

    if (usersFromDB && !user.name) {
      const thisUserFromDB = usersFromDB[0];

      if (thisUserFromDB?.name) {
        dispatch(setUser(thisUserFromDB))
        dispatch(setIsAuth(true))

      } else goToRegistration()
    }
  }, [usersFromDB]);

  useEffect(() => {
    if (location.pathname === NavigatePath(registrationPath)) return;
    if (isAuth === false) navigate(NavigatePath(registrationPath))
    if (isAuth !== null) setIsLoaderActive(false)
  }, [location.pathname, isAuth]);

  useEffect(() => {
    if (!isAuth) return;

    const localStorageKey = 'notificationLastDate'
    const lastDate = localStorage.getItem(localStorageKey);
    if (daysDifference(time, lastDate) <= 0) return;

    const date = new Date(time);
    const userWasCameIn = daysDifference(time, user.lastActivity) === 0;

    // Проверка, находится ли время между 16 и 20 часами и заходил 
    //ли пользователь сегодня
    if (date.getHours() >= 16 && date.getHours() < 20 && !userWasCameIn) {
      const title = 'Время для новых привычек!!!'
      const options = {
        body: 'Зайдите в habits app чтобы научиться новым привычкам.',
        icon: logo
      }
      createNotification(title, options)
      localStorage.setItem(localStorageKey, time)
    }
  }, [time, isAuth])

  useEffect(() => {
    const millisecondsInHours = 3600000
    const intervalTime = millisecondsInHours

    const interval = setInterval(() => {
      const date = new Date(time)
      const newDate = new Date(date.getTime() + intervalTime)
      dispatch(setDateAction(newDate.toISOString()))
    }, intervalTime);
    return () => clearInterval(interval);
  }, [time]);

  return (
    <div className="App">
      {isLoaderActive ? <LoaderWindow /> : ''}

      <AppRouter />
      <UserActivity user={usersFromDB?.at(0)} />
      {modal.type === ADD_ACTION_MODAL && <AddActionModal data={modal.data} />}
    </div>
  );
}

export default App;
