import React, { memo, useCallback, useEffect, useState } from 'react';
import './AddHabitsForm.css'
import ButtonBlue from '../../ui/ButtonBlue/ButtonBlue';
import { addAction, addHabit, usersHabitsCoinCost, usersHabitsExpCost } from '../../db';
import { useSelector } from 'react-redux';

const AddHabitsForm = memo(() => {
    const time = useSelector(state => state.date)
    const isAuth = useSelector(state => state.isAuth)

    const [menuStatus, setMenuStatus] = useState('');

    const [textHabit, setTextHabit] = useState('');
    const minTextLength = 5;

    const [targetValue, setTargetValue] = useState(null);
    const [category, setCategory] = useState('');
    const [period, setPeriod] = useState('daily');

    const [fileInput, setFileInput] = useState(null);

    const [error, setError] = useState(null);

    useEffect(() => {
        const check = () => {
            if (targetValue && !Number(targetValue))
                return (<p className='error'>Численое значение должно быть числом (и больше нуля)!!!</p>);
            return null;
        }
        setError(check())
    }, [targetValue]);

    const openMenu = () => setMenuStatus(menuStatus ? '' : 'active');

    const addHabitsEvent = (e) => {
        e.preventDefault()

        if (!textHabit && !fileInput) return;
        if (!isAuth) return;
        const inputsValues = {
            title: textHabit,
            category,
            period,
            targetValue,
        }
        addHabit(inputsValues, time)
        addUseFile()

        setTextHabit('')
        setCategory('')
        setTargetValue(null)
        setPeriod('daily')
        setFileInput(null)

        document.getElementById('AddHabitsForm').reset();
    }

    const addUseFile = async () => {
        if (!fileInput) return;
        try {
            const reader = new FileReader();

            reader.onload = function (event) {
                const content = event.target.result;
                const jsonData = JSON.parse(content);
                const { habits, actions } = jsonData;

                habits.forEach(element => {
                    const { title, category, period, targetValue, addDate } = element;
                    const values = {
                        title,
                        category,
                        period,
                        targetValue,
                    }
                    addHabit(values, addDate)
                });

                actions.forEach(element => {
                    const { id, value, date } = element;
                    addAction(id, value, date)
                })
            };

            reader.readAsText(fileInput);
        } catch (error) {
            setError('Проверь файл там что-то неправильно')
        }
    }

    const textChange = (e) => setTextHabit(e.target.value);
    const targetValueChange = (e) => setTargetValue(e.target.value)
    const categoryChange = (e) => setCategory(e.target.value)
    const periodChange = (e) => setPeriod(e.target.value)

    const fileChange = (e) => setFileInput(e.target.files[0]);
    return (
        <article className='AddHabitsForm'>
            <button className='AddHabitsForm-header' onClick={openMenu}>
                <div className='AddHabitsForm-title'>
                    <h3>Создайте новую привычку!</h3>
                </div>
                <div className={`openArrow ${menuStatus}`}>
                    <svg width="16" height="9" viewBox="0 0 16 9" fill="none" xmlns="http://www.w3.org/2000/svg">
                        <path d="M15 1L8 8L1 1" stroke="black" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" />
                    </svg>
                </div>
            </button>

            <div className={`AddHabitsForm-content ${menuStatus}`}>
                <form id='AddHabitsForm' onSubmit={addHabitsEvent}>
                    <p className='description'>
                        Напишите какую привычку хотите создать, созданые привычки очениваются в
                        {usersHabitsExpCost} опыт и в {usersHabitsCoinCost} чокопаев.
                    </p>

                    <input onChange={textChange} type='text' className='text-input' placeholder='Писать здесь...' />

                    <div className='secondaryInputs-box'>
                        <input onChange={targetValueChange} className='text-input small-input' placeholder='Численое значение' />
                        <input onChange={categoryChange} className='text-input small-input' placeholder='Категория' />

                        <select className='period-input small-input' defaultValue='daily' onChange={periodChange}>
                            <option value='daily'>Дневной</option>
                            <option value='weekly'>Недельный</option>
                            <option value='monthly'>Месячный</option>
                        </select>
                    </div>

                    <input accept='application/json' onChange={fileChange} id='AddHabitsForm-fileInput' type="file" />
                    <label className='fileInput' htmlFor='AddHabitsForm-fileInput'>
                        Загрузить файл в формате JSON
                    </label>

                    {error}

                    <ButtonBlue
                        disabled={textHabit.length < minTextLength}
                        onClick={addHabitsEvent}
                        className='submit-btn'>Создать</ButtonBlue>
                </form>
            </div>
        </article>
    );
})

export default AddHabitsForm;

