import React, { useState } from 'react';
import './Registration.css';
import { db } from '../../db';
import { useNavigate } from 'react-router-dom';
import { NavigatePath, listPath } from '../../routes';
import { useDispatch } from 'react-redux';
import { setIsAuth, setUser } from '../../store';
import { levelSettings } from '../../gamification';

const Registration = () => {
    const navigate = useNavigate()
    const dispatch = useDispatch()
    const [name, setName] = useState('');

    const sumbit = async (e) => {
        try {
            e.preventDefault()
            if (!name.length) return;

            await db.user.clear();
            const userObject = {
                name,
                level: 1,
                coins: 0,
                health: levelSettings[0].maxHealth,
                experience: 0,
                daysActivity: 0,
                lastActivity: null,
                openChestDate: null,
                openChestDate: null
            }
            await db.user.add(userObject)

            dispatch(setUser(userObject))
            dispatch(setIsAuth(true))

            navigate(NavigatePath(listPath))
        } catch (error) {
            console.log(error)
        }
    }

    const nameChange = (e) => setName(e.target.value)
    return (
        <div className='registration'>
            <main>
                <form onSubmit={sumbit}>
                    <div className='title-box'>
                        <h1>Регистрация</h1>
                    </div>
                    <div className='input-box'>
                        <h3 className='input-placeholder'>Имя</h3>
                        <input onChange={nameChange} placeholder='Писать здесь' />
                    </div>
                    <div className='submit-btn-box'>
                        <button onClick={sumbit} disabled={!name.length}>Продолжить</button>
                    </div>
                </form>
            </main>
        </div>
    );
}

export default Registration;
