import React from 'react';
import useModal from '../hooks/useModal';
import '../commonStyles/Modal.css'

const ModalLayout = ({ modalTitle, children }) => {
    const [closeModal, closeOnClick] = useModal(null, '.modal-wrapper');
    return (
        <div className='modal-wrapper' onClick={closeOnClick}>
            <div className='modal'>
                <div className='modal-header'>
                    <div className='header-title'>
                        <h2>{modalTitle}</h2>
                    </div>
                    <button className='close-modal' onClick={closeModal}>
                        <svg width="14" height="14" viewBox="0 0 14 14" fill="none" xmlns="http://www.w3.org/2000/svg">
                            <path d="M1 13L13 1M1 1L13 13" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" />
                        </svg>
                    </button>
                </div>
                <div className='modal-contant'>
                    {children}
                </div>
            </div>
        </div>
    );
}

export default ModalLayout;
