import React, { useEffect } from 'react';
import './UserEventsLayout.css'
import { useSelector } from 'react-redux';

const UserEventsLayout = ({ closeEvent, children }) => {
    const modalType = useSelector(state => state.openModule)

    useEffect(() => {
        const classList = document.querySelector('body').classList;
        classList.add('modalActive')
        return () => {
            if (!modalType.type) {
                classList.remove('modalActive')
            }
        };
    }, []);

    return (
        <div className='UserEventsLayout'>
            <div className='container'>
                <button className='close-btn' onClick={closeEvent}>
                    <svg width="14" height="14" viewBox="0 0 14 14" fill="none" xmlns="http://www.w3.org/2000/svg">
                        <path d="M1 13L13 1M1 1L13 13" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" />
                    </svg>
                </button>
                <article>
                    {children}
                </article>
            </div>
        </div>
    );
}

export default UserEventsLayout;
