import React from 'react';
import CoinsView from '../../../ui/CoinsView/CoinsView';
import chocoPie from '../../../images/chocoPie.png';
import UserEventsLayout from '../UserEventsLayout';

const OpenChest = ({ data, setEvent }) => {
    const { plusCoins, coins } = data;

    const closeMenu = () => setEvent(null)
    return (
        <UserEventsLayout closeEvent={closeMenu}>
            <div className='UserEventsLayout-item OpenChest'>
                <div className='image-box'>
                    <img src={chocoPie} />
                </div>
                <div className='content-box'>
                    <p className='event-title'>
                        Вам выпало {plusCoins} чокопаев. Теперь у вас
                    </p>
                    <CoinsView count={coins + plusCoins} />
                    <div className='manage-btns-box'>
                        <button onClick={closeMenu} className='manage-btns_continue'>Продолжить</button>
                    </div>
                </div>
            </div>
        </UserEventsLayout>
    );
}

export default OpenChest;
