import React, { useState } from 'react';
import './ActionHistory.css'
import ActionsList from '../../ui/ActionsList/ActionsList';

const ActionHistory = ({ habitId }) => {
    const [historyOpen, setHistoryOpen] = useState();
    const openHistoryMenu = () => setHistoryOpen(historyOpen ? false : true)

    return (
        <div className='actions-history' onClick={openHistoryMenu}>
            <button className={`actions-history-header ${historyOpen ? 'active' : ''}`}>
                <div className='header-title'>
                    <h3>Просмотр истроии действий!</h3>
                </div>
                <svg width="16" height="9" viewBox="0 0 16 9" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <path d="M15 1L8 8L1 1" stroke="black" strokeLinecap="round" strokeLinejoin="round" />
                </svg>
            </button>
            <div className={`actions-history-content ${historyOpen ? 'active' : ''}`}>
                <ActionsList habitId={habitId} />
            </div>
        </div>
    );
}

export default ActionHistory;
