import React, { useMemo, useState } from 'react';
import SetFilterBtn from '../../ui/SetFilterBtn/SetFilterBtn';
import { habitsLibrary } from './habitsLibrary';
import './Library.css'
import HabitFromLibrary from '../../components/HabitFromLibrary/HabitFromLibrary';

const Library = () => {
    const categoriesList = useMemo(() => {
        return habitsLibrary?.map(({ key, category }) => ({ key, category }))
    }, [habitsLibrary])
    const [currentCategory, setCurrentCategory] = useState('PhysicalActivity');

    const habits = useMemo(() => {
        const categoryObj = habitsLibrary.find(({ key }) => key === currentCategory)

        return categoryObj.habits.map(data => ({
            ...data,
            category: categoryObj.category
        }))
    }, [currentCategory])

    return (
        <div className='Library'>
            <div className='selectCategory'>
                <div className='selectCategory-title'>
                    <h1>Библиотека привычек</h1>
                </div>
                <div className='selectCategory-categories'>
                    {categoriesList.map(({ key, category }) => {
                        const props = {
                            filter: currentCategory,
                            setFilter: setCurrentCategory,
                            filterKey: key,
                            key
                        }
                        return (<SetFilterBtn {...props}>{category}</SetFilterBtn>)
                    })}
                </div>
            </div>
            <div className='Library-habits'>
                {habits.map(data =>
                    <HabitFromLibrary key={data.title} habit={data} />
                )}
            </div>
        </div>
    );
}

export default Library;
