import React, { useMemo, useState } from 'react';
import { HabitElementIcons } from '../HabitElement/icons';
import './HabitFromLibrary.css'
import { addHabit } from '../../db';
import { useSelector } from 'react-redux';

const HabitFromLibrary = ({ habit }) => {
    const time = useSelector(state => state.date)
    const [habitWasAdded, setHabitWasAdded] = useState(false);
    const { title, category, period, targetValue, priceCoin, priceExp } = habit;

    const periodRus = useMemo(() => {
        switch (period) {
            case 'daily':
                return 'Дневной'
            case 'weekly':
                return 'Недельный'
            case 'monthly':
                return 'Месячный'
        }
    }, [period])

    const declensionChocoPie = (count) => {
        if (count === 1) return 'чокопай';
        if (count < 5) return 'чокопая';
        return 'чокопаев'
    }

    const addHabitToList = async () => {
        addHabit(habit, time)
        setHabitWasAdded(true)
    }

    return (
        <div className='HabitFromLibrary'>
            <div className='HabitFromLibrary-content'>
                <div className='HabitFromLibrary-header'>
                    <h3 className='habit-title'>{title}</h3>
                </div>
                <div className='HabitFromLibrary-info-box'>
                    <div className='habit-info'>
                        {HabitElementIcons.prize}
                        <h4>
                            За выполнение вы получите {priceExp} опыта и {
                                priceCoin
                            } {declensionChocoPie(priceCoin)}
                        </h4>
                    </div>
                    <div className='habit-info'>
                        {HabitElementIcons.period}
                        <h4>{periodRus}</h4>
                    </div>
                    {targetValue ?
                        <div className='habit-info'>
                            {HabitElementIcons.target}
                            <h4>{targetValue}</h4>
                        </div> : ''
                    }
                    <div className='habit-info'>
                        {HabitElementIcons.category}
                        <h4>{category}</h4>
                    </div>
                </div>
            </div>
            <div className='HabitFromLibrary-manageMenu'>
                <button className='manageMenu-getButton'
                    disabled={habitWasAdded} onClick={addHabitToList}>

                    <svg width="20" height="20" viewBox="0 0 20 20" fill="none" xmlns="http://www.w3.org/2000/svg">
                        <path d="M10 7V10M10 10V13M10 10H13M10 10H7M19 10C19 11.1819 18.7672 12.3522 18.3149 13.4442C17.8626 14.5361 17.1997 15.5282 16.364 16.364C15.5282 17.1997 14.5361 17.8626 13.4442 18.3149C12.3522 18.7672 11.1819 19 10 19C8.8181 19 7.64778 18.7672 6.55585 18.3149C5.46392 17.8626 4.47177 17.1997 3.63604 16.364C2.80031 15.5282 2.13738 14.5361 1.68508 13.4442C1.23279 12.3522 1 11.1819 1 10C1 7.61305 1.94821 5.32387 3.63604 3.63604C5.32387 1.94821 7.61305 1 10 1C12.3869 1 14.6761 1.94821 16.364 3.63604C18.0518 5.32387 19 7.61305 19 10Z" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" />
                    </svg>
                </button>
            </div>
        </div>
    );
}

export default HabitFromLibrary;
